<template>
  <div class="detailWrap">
    <Detaiheader :liked="detailed.liked_count" :recs="detailed.recs_count" :isShow="true"></Detaiheader>
    <h2>{{ detailed.title }}</h2>
    <div v-html="detailed.content"></div>
  </div>
</template>

<script>
  import Detaiheader from  '../../components/detaiheader'
  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export default {
    components: {
      Detaiheader
    },
    data: function (){
      return {
        detailed: []
      }
    },
    created: function (){
      var id = this.$route.params.id;
      console.log(id)
      request.get("https://api.douban.com/v2/note/"+id+"?format=html_full")
        .use(jsonp)
        .end((err, res) => {
          if (!err){
            console.log(res.body)
            this.detailed = res.body
          }
      })
    }
  }
</script>

<style>
  .detailWrap {padding-bottom: 1.47rem;position: relative;}
  .detailWrap h2 {color: #111;font-size: .8rem;text-align: center;padding-top: 1.34rem;}
  .detailWrap div {font-size: .5rem;}
  .detailWrap div p {margin-bottom: .2rem;line-height: 1rem;}
  .detailWrap div .image-caption-wrapper {text-align: center;color: #999;}
  .detailWrap img {width: 100%;}
</style>



