<template>
  <div class="recomitemWrap">
    <div class="title clearfix">
      <!--url-->
      <slot name="url"></slot>
    </div>
    <div class="content">
      <h4>{{ htxt }}</h4>
      <p>{{ hptxt }}</p>
      <div class="icon clearfix">
        <!---->
        <slot name="portrait"></slot>
        <h5>{{ name }}</h5>
        <p>{{ introduce }}</p>
      </div>
    </div>
    <a href="javascript:;" class="bottom"></a>
  </div>
</template>

<script>
  export default {
    props: {
      "htxt": {
        type: String,
        default: ""
      },
      "hptxt": {
        type: String,
        default: "100"
      },
      "name": {
        type: String,
        default: ""
      },
      "introduce": {
        type: String,
        default: ""
      }
    }
  }
</script>

<style scoped>
  .recomitemWrap {width: 8.08rem;height: 7.35rem;background: #fff;border-radius: .15rem;margin-left: .13rem;overflow: hidden;margin-bottom: .64rem;}
  .recomitemWrap .title {width: 8.08rem;height: 2.71rem;}
  .recomitemWrap .title img {width: 100%;height: 100%;float: left;}
  .recomitemWrap .content {width: 7.2rem;padding-left: .44rem;padding-bottom: .38rem;}
  .recomitemWrap .content h4 {font-size: .42rem;color: #494949;height: .66rem;line-height: .66rem;padding-top: .27rem;}
  .recomitemWrap .content p {font-size: .3rem;color: #9b9b9b;height: .56rem;line-height: .56rem;padding-bottom: .17rem;}
  .recomitemWrap .icon img {width: 1.2rem;height: 1.2rem;border-radius: 50%;float: left;}
  .recomitemWrap .icon h5 {font-size: .34rem;color: #494949;margin-left: 1.48rem;}
  /*16*/
  .recomitemWrap .icon p {margin-left: 1.48rem;overflow: hidden;white-space: nowrap;text-overflow:ellipsis;width: 16em;}
  .recomitemWrap .bottom {display: block;width: 7.2rem;height: .94rem;border-radius: 8px;border: 1px solid #43bd57;font-size: .34rem;color: #43bd57;text-align: center;line-height: .94rem;}
</style>



