<template>
  <div class="filmpage5Wrap">
    <Smallnav txt=""></Smallnav>
    <div class="icon clearfix">
      <Cinema :txt="item.title" :num="item.rating.average" class="icon_pic" v-for="item in info" :key="item.id">
        <img :src="item.images.small" alt="" slot="icon"/>
        <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
        <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
        <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
        <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
        <img src="../../assets/images/rating_star_xsmall_half.png" alt="" slot="star"/>
      </Cinema>
    </div>
    <div class="font">
      <a href="javascript:;" @click="changeFn()"> ></a>
    </div>
  </div>
</template>

<script>
  import Smallnav from "../../components/smallnav"
  import Cinema from "../../components/cinema"
  export default {
    components: {
      Smallnav,
      Cinema
    },
    data (){
      return {
        info: []
      }
    },
    mounted (){
      this.$jsonp("http://api.douban.com/v2/movie/in_theaters?start=0&count=6").then(data => {
        this.info = data.subjects
      })
    },
    methods: {
      changeFn: function (){
        this.$router.push({path: "detaimovie"})
      }
    }
  }
</script>

<style scoped>
  .filmpage5Wrap {background: #fff;margin-bottom: .3rem;}
  .icon .icon_pic {float: left;margin-left: .78rem;margin-bottom: 1rem;width: 3rem;}
  .icon .icon_pic1 {margin-left: .9rem;}
  .filmpage5Wrap .font {text-align: center;}
  .filmpage5Wrap a {display: inline-block;margin: .43rem 0 .7rem;color: #42bd56;font-size: .36rem;}
</style>



