<template>
  <div class="itemWrap" :class="{active:!bol}" @click="clickFn">
    <span v-show="bol"><slot name="normalImg"></slot><br /></span>
    <span v-show="!bol"><slot name="activeImg"></slot><br /></span>
    <span>{{ txt }}</span>
  </div>
</template>

<script>
export default {
  name: "itemWrap",
  props: ["txt","mark","selecte"],
  methods: {
    clickFn: function () {
      console.log(this.mark);
      this.$emit("change",this.mark);
      this.$router.push("/"+ this.mark);
    }
  },
  computed: {
    bol: function () {
      if (this.mark==this.selecte){
        return false;
      }
      return true;
    }
  }
}
</script>

<style scoped>
  .itemWrap {float: left;text-align: center;width: 20%;font-size: .3rem;line-height: .4rem;}
  .itemWrap img {width: .96rem;}
  .itemWrap span {color: #ccc;}
  .itemWrap.active span {color: #51BF63;}
</style>



