<template>
  <div class="tabbar">
    <Tabbaritem txt="" mark="home" :selecte="selected" @change="getVal">
      <img src="../assets/images/ic_tab_home_normal.png" alt="" slot="normalImg"/>
      <img src="../assets/images/ic_tab_home_active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="audio" :selecte="selected" @change="getVal">
      <img src="../assets/images/ic_tab_subject_normal.png" alt="" slot="normalImg"/>
      <img src="../assets/images/ic_tab_subject_active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="broadcast" :selecte="selected" @change="getVal">
      <img src="../assets/images/ic_tab_status_normal.png" alt="" slot="normalImg"/>
      <img src="../assets/images/ic_tab_status_active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="group" :selecte="selected" @change="getVal">
      <img src="../assets/images/ic_tab_group_normal.png" alt="" slot="normalImg"/>
      <img src="../assets/images/ic_tab_group_active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="mine" :selecte="selected" @change="getVal">
      <img src="../assets/images/ic_tab_profile_normal.png" alt="" slot="normalImg"/>
      <img src="../assets/images/ic_tab_profile_active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
  </div>
</template>

<script>
  import Tabbaritem from "./tabbaritem.vue"
export default {
  name: "tabbar",
  components: {
    Tabbaritem
  },
  data: function () {
    return {
      selected: "home"
    }
  },
  methods: {
    getVal: function (val) {
      console.log(val);
      this.selected=val;
    }
  }
}
</script>

<style scoped>
  .tabbar {width: 100%;height: 1.47rem;position: fixed;left: 0;bottom: 0;background: #F7F7F7;border-top: 1px solid #ccc;color: #969696;z-index: 2;}
</style>



