<template>
  <div class="movieDetaiWrap">
    <Publicheader  txt="">
      <span slot="btn" class="btn" @click="backFn()"></span>
    </Publicheader>
    <div>
      <h2>{{ info.title }}</h2>
      <div class="movielist">
        <img :src="info.images.medium" alt="">
      </div>
      <div class="right">
        <p><a href="javascript:;">{{ tostarFn(info.directors) }}</a></p>
        <p><a href="javascript:;">{{ tostarFn(info.casts) }}</a></p>
        <p><a href="javascript:;">{{ info.genres }}</a></p>
        <p>/{{ info.countries }}</p>
        <p>{{ info.aka }}</p>
        <p></p>
        <p><a href="javascript:;">{{ info.rating.average }}</a></p>
        <p>{{ info.ratings_count }}</p>
        <p>{{ info.summary }}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import Publicheader from "../../components/publicheader"

  import request from 'superagent'
  import jsonp from 'superagent-jsonp'

  export default {
    components: {
      Publicheader
    },
    methods: {
      backFn: function (){
        this.$router.go(-1)
      },
      tostarFn: function (obj){
        var str = "";
//        arr.forEach(function (ele){
//          str += ele.name+"/"
//        })
//        console.log(typeof obj)
        for (var i = 0 ; i < obj.length ; i++){
          str += obj[i].name+"/"
        }
        return str
      }
    },
    data: function (){
      return {
        info: {
          images: {},
          directors: [
            {}
          ],
          casts: [
            {}
          ],
          genres: [
            {}
          ],
          countries: [
            {}
          ],
          rating: ""
        },
        img: ""
      }
    },
    created(){
      var id = this.$route.params.id;
      var _this = this;
//      console.log(id)
//      request.get("http://api.douban.com/v2/movie/subject/"+id).use(jsonp)
//        .end((err, res) => {
//          if (!err){
////          console.log(this)
//        this.info = res.body;
//      }
//      })
      //vue-jsonp 
      this.$jsonp('http://api.douban.com/v2/movie/subject/'+id).then(data => {
        this.info = data
//        console.log(data)
      })
    }
  }
</script>

<style scoped>
  .movieDetaiWrap {padding-top: 1.34rem;}
  .movieDetaiWrap .right {font-size: .5rem;}
  .movieDetaiWrap h2 {font-size: 1rem;padding: .3rem 0;text-align: center;}
</style>



