<template>
  <div class="filmpage4Wrap">
    <Smallnav txt="">
      <a href="javascript:;" slot="more" @click="changeFn()">></a>
    </Smallnav>
    <Carousel  swiperid="swipe06" paginationDirection="right" :pagination=false :autoplay=0 :loop=false slidesPerView="auto">
      <div class="swiper-slide" slot="swiper-con" v-for="item in info">
        <router-link :to="{name: 'MovieDetai', params: {id: item.id}}">
          <Cinema :txt="item.title" evaluate="">
            <img :src="item.images.small" alt="" slot="icon"/>
            <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
            <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
            <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
            <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
            <img src="../../assets/images/rating_star_xsmall_half.png" alt="" slot="star"/>
          </Cinema>
        </router-link>
      </div>
    </Carousel>
  </div>
</template>

<script>
  import Smallnav from "../../components/smallnav"
  import Hot from "../../components/hot"
  import Carousel from "../../components/carousel"
  import Find from "../../components/find"
  import Cinema from "../../components/cinema"
  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export default {
    components: {
      Smallnav,
      Hot,
      Carousel,
      Find,
      Cinema
    },
    data: function (){
      return {
        info: []
      }
    },
    mounted: function (){
//      request.get("http://api.douban.com/v2/movie/in_theaters?start=0&count=8")
//        .use(jsonp)
//        .end((err, res) => {
//          if (!err){
//          this.info = res.body.subjects
//        console.log(res.body)
//      }
//      })
      this.$jsonp("http://api.douban.com/v2/movie/in_theaters?start=0&count=8").then(data => {
        this.info = data.subjects
//        console.log(data)
      })
    },
    methods: {
      changeFn: function (){
        this.$router.push({path: "detaimovie"})
      }
    }
  }
</script>

<style scoped>
  .filmpage4Wrap {background: #fff;padding-bottom:  .54rem;margin-bottom: .3rem;}
</style>



