<template>
  <div class="audioWrap">
    <Publicheader txt="">
      <img src="../../assets/images/ic_group_search.png" alt="" class="left" slot="left"/>
      <img src="../../assets/images/ic_create_group_chat_blue.png" alt="" class="right" slot="right"/>
    </Publicheader>
    <ul class="stagenav clearfix">
      <li :class="{active: nowIndex==1}" @click="nowIndex=1">
        <router-link to="/audio/film"></router-link>
      </li>
      <li :class="{active: nowIndex==2}" @click="nowIndex=2">
        <router-link to="/audio/read"></router-link>
      </li>
      <li :class="{active: nowIndex==3}" @click="nowIndex=3">
        <router-link to="/audio/tv"></router-link>
      </li>
      <li :class="{active: nowIndex==4}" @click="nowIndex=4">
        <router-link to="/audio/city"></router-link>
      </li>
      <li :class="{active: nowIndex==5}" @click="nowIndex=5">
        <router-link to="/audio/music"></router-link>
      </li>
    </ul>
    <div class="stagenavcontent">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import Publicheader from "../../components/publicheader"
  export default {
    components: {
      Publicheader
    },
    data: function () {
      return {
        nowIndex: 1
      }
    }
  }
</script>

<style scoped>
  .audioWrap ul {border-bottom: 1px solid #DFDFDF;background: #fff;position: fixed;left: 0;top: 1.35rem;width: 100%;z-index: 10;}
  .audioWrap li {float: left;width: 20%;font-size: .43rem;text-align: center;line-height: 1.13rem;border-bottom: .06rem solid transparent;height: 1.13rem;}
  .audioWrap li a {color: #9B9B9B;}
  .audioWrap .active {border-color: #6BCB7B;}
  .audioWrap .active a {color: #6BCB7B}
  .stagenavcontent {padding-top: 2.48rem;}
</style>



