import Vue from 'vue'
import Router from 'vue-router'

//axios
import Axios from '../assets/libs/axios/dist/axios.min.js'
import VueAxios from '../assets/libs/vue-axios/dist/vue-axios.min.js'


//tabbar
import Tabbar from '@/components/tabbar'
import Home from '@/pages/home/home'
import Audio from '@/pages/audio/audio'
import Mine from '@/pages/mine/mine'
import Group from '@/pages/group/group'
import Broadcast from '@/pages/broadcast/broadcast'

//audio
import Film from '@/pages/audio/film'
import Read from '@/pages/audio/read'
import Tv from '@/pages/audio/tv'
import City from '@/pages/audio/city'
import Music from '@/pages/audio/music'

//
import Detail from '@/pages/details/detail'
import Details from '@/pages/details/details'
import Detail1 from '@/pages/detail1/detail1'
import Detaimovie from '@/pages/detail1/detaimovie'
import MovieDetai from '@/pages/details/movieDetai'

Vue.use(Router)
Vue.use(VueAxios, Axios)

export default new Router({
  routes: [
    {
      path: '/',//home
      name: 'Home',
      components: {
        default: Home,
        Tabbar
      }
    },
    {
      path: '/home',
      name: 'Home',
      components: {
        default: Home,
        Tabbar
      }
    },
    {
      path: '/audio',
      name: 'Audio',
      components: {
        default: Audio,
        Tabbar
      },
      children: [
        {path: '', component: Film},//audioFilm
        {path: 'film', component: Film},
        {path: 'read', component: Read},
        {path: 'tv', component: Tv},
        {path: 'city', component: City},
        {path: 'music', component: Music}
      ]
    },
    {
      path: '/group',
      name: 'Group',
      components: {
        default: Group,
        Tabbar
      }
    },
    {
      path: '/mine',
      name: 'Mine',
      components: {
        default: Mine,
        Tabbar
      }
    },
    {
      path: '/broadcast',
      name: 'Broadcast',
      components: {
        default: Broadcast,
        Tabbar
      }
    },
    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/details/:id',
      name: 'Details',
      component: Details
    },
    {
      path: '/movieDetai/:id',
      name: 'MovieDetai',
      component: MovieDetai
    },
    {
      path: '/detail1',
      name: 'Detail1',
      component: Detail1
    },
    {
      path: '/detaimovie',
      name: 'Detaimovie',
      component: Detaimovie
    }
  ]
})



