<template>
  <div class="detaiheaderWrap">
    <span @click="goBack()"></span>
    <span class="link" @click="addl()" v-show="isShow"></span>
    <i>{{ liked }}</i>
    <span class="reply" @click="addr()" v-show="isShow"></span>
    <i class="replyi">{{ recs }}</i>
    <span class="forword" @click="forFn()"></span>
  </div>
</template>

<script>
  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export default {
    props: ["liked","recs","isShow"],
    methods: {
      goBack: function (){
        this.$router.go(-1);
      },
      addl: function (){
        this.liked++
      },
      addr: function (){
        this.recs++
      },
      forFn: function (){
        alert("")
      }
    }
  }
</script>

<style scoped>
  .detaiheaderWrap {background: #EDF4ED;height: 1.34rem;position: fixed;top: 0;left: 0;z-index: 1;width: 100%}
  .detaiheaderWrap span {display: block;width: .8rem;height: .8rem;background: url(../assets/images/ic_bar_back_green.png) no-repeat;background-size: cover;position: absolute;left: .5rem;top: .27rem;}
  .detaiheaderWrap .link {background-image: url(../assets/images/ic_menu_like.png);left: 8rem;}
  .detaiheaderWrap i {margin-top: -.5rem;font-size: .1rem;color: green;position: absolute;left: 8.8rem;top: .8rem;}
  .detaiheaderWrap .reply {background-image: url(../assets/images/ic_menu_reply.png);left: 9.5rem;}
  .detaiheaderWrap .replyi {left: 10.3rem;}
  .detaiheaderWrap .forword {background-image: url(../assets/images/ic_action_share_green.png);left: 11rem;}
</style>



