<template>
  <div>
    <!--<div class="hotWrap" :class="{border_bottom: bol}" @click="clickFn">-->
    <div class="hotWrap"  v-for="item in hotNews" :key="item.id">
      <router-link :to="{name: 'Detail', params:{id: item.id}}">
        <!---->
        <h3>{{ item.title }}</h3>
        <p class="top_p">{{ item.summary }}</p>
        <!---->
        <span class="underline"></span>
        <!---->
        <p class="bot_lp">{{ item.author.name }}</p>
        <!---->
        <!--<p class="bot_rp">{{ brptxt }}</p>-->
        <!---->
        <img :src="item.images[1].url" alt="">
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
//    props: ["htxt","tptxt","blptxt","brptxt","bol","id"],
    props: {
      hotNews:{
        type: Array,
        required: true
      }
    }
  }
</script>

<style scoped>
  .hotWrap {position: relative;}
  .hotWrap.border_bottom {border-bottom: 1px solid #dfdfdf;margin-bottom: .5rem;}
  .hotWrap h3 {line-height: .74rem;font-size: .5rem;padding-left: .62rem;width: 7.7rem;color: #494949;}
  .hotWrap p {line-height: .56rem;color: #9B9B9B;padding-left: .62rem;}
  .hotWrap .top_p {font-size: .36rem;padding-top: .27rem;width: 7.7rem;overflow:hidden; text-overflow:ellipsis;display:-webkit-box; -webkit-box-orient:vertical;-webkit-line-clamp:2;}
  .hotWrap .underline {display: block;width: .45rem;height: 1px;background: #CCCCCC;margin: .27rem 0 .24rem .62rem;}
  .hotWrap .bot_lp {font-size: .38rem;padding-bottom: .51rem;color: #D5D5D5;}
  .hotWrap .bot_rp {font-size: .38rem;color: #D5D5D5;position: absolute;right: .6rem;bottom: .51rem;}
  .hotWrap img {width: 2.7rem;position: absolute;top: .11rem;right: .6rem;}
</style>



