<template>
  <div class="detailsWrap">
    <Detaiheader></Detaiheader>
    <h2 class="center">{{ detailed.title }}</h2>
    <p class="center">{{ detailed.begin_time }}</p>
    <div class="pic clearfix">
      <img :src="detailed.image" alt="">
    </div>
    <span>{{ detailed.desc }}</span>
  </div>
</template>

<script>
  import Detaiheader from  '../../components/detaiheader'
  export default {
    components: {
      Detaiheader
    },
    data: function (){
      return {
        detailed: {}
      }
    },
    created: function (){
      var _this = this;
      var id = this.$route.params.id;
      console.log(id)
      this.axios.get("/static/data/onLines.json").then(function (res) {
//        console.log(res.data)
        res.data.onlines.forEach(function (ele){
          if (id == ele.album_id){
            _this.detailed=(ele);
          }
        })
        console.log(_this.detailed)
      })
    }
  }
</script>

<style scoped>
  .detailsWrap {padding-top: 1.34rem;}
  .detailsWrap h2 {font-size: .8rem;}
  .detailsWrap p {font-size: .5rem;}
  .center {text-align: center;}
  .pic {margin: .3rem 0;}
  .pic img {width: 100%;float: left;}
  span {font-size: .5rem;color: #999;}
</style>



