<template>
  <div class="filmpage1Wrap">
    <Smallnav txt="">
      <a href="javascript:;" slot="more" @click="changeFn()">></a>
    </Smallnav>
    <Carousel  swiperid="swipe04" paginationDirection="right" :pagination=false :autoplay=0 :loop=false slidesPerView="auto">
      <div class="swiper-slide" slot="swiper-con" v-for="item in info" :key="item.id">
       <router-link :to="{name: 'MovieDetai', params:{id: item.id}}">
         <Cinema :txt="item.title" :num="item.rating.average">
           <img :src="item.images.small" alt="" slot="icon"/>
           <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
           <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
           <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
           <img src="../../assets/images/rating_star_xsmall_on.png" alt="" slot="star"/>
           <img src="../../assets/images/rating_star_xsmall_half.png" alt="" slot="star"/>
         </Cinema>
       </router-link>
      </div>
    </Carousel>
    <Advertisement txt="">
      <img src="../../assets/images/IMG_0380_02.png" alt="" slot="adver"/>
    </Advertisement>
  </div>
</template>

<script>
  import Smallnav from "../../components/smallnav"
  import Carousel from "../../components/carousel"
  import Cinema from "../../components/cinema"
  import Advertisement from "../../components/advertisement"

  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export  default {
    components: {
      Smallnav,
      Carousel,
      Cinema,
      Advertisement
    },
    data: function (){
      return {
        info: []
      }
    },
    mounted: function(){
      var _this=this;
//      this.axios.get("/api/movie/in_theaters?start=0&count=8").then(function (res){
////        console.log(res)
//        _this.info = res.data.subjects;
//        console.log(_this.info)
//      })
      request.get("http://api.douban.com/v2/movie/in_theaters?start=0&count=8")
        .use(jsonp)
        .end((err, res) => {
          if (!err){
          this.info = res.body.subjects
//          console.log("",res)
          }
      })
    },
    methods: {
      changeFn: function (){
        this.$router.push({path: "detaimovie"})
      }
    }
  }
</script>

<style scoped>
  .filmpage1Wrap {background: #fff;margin-top: .32rem;}
</style>



