<template>
    <div class="swiper-container" :class="swiperid">
      <div class="swiper-wrapper">
        <slot name="swiper-con"></slot>
      </div>
      <div :class="{'swiper-pagination': pagination}" :style="{'text-align': paginationDirection}"></div>
    </div>
</template>

<script>
import '../assets/libs/swiper/js/swiper.min.js'
  export default {
  props: {
    swiperid: {
      type: String,
      default: ""
    },
    effect: {
      type: String,
      default: "slide"
    },
    loop: {
      type: Boolean,
      default: true
    },
    diraction: {
      type: String,
      default: "horizontal"
    },
    autoplay: {
      type: Number,
      default: 5000
    },
    paginationType: {
      type: String,
      default: "bullets"
    },
    pagination: {
      type: Boolean,
      default: true
    },
    paginationDirection: {
      type: String,
      default: "center"
    },
    loopedSlides: {
      type: Number,
      default: 1
    },
    slidesPerView: {
      type: [Number, String],
      default: 1
    },
    observer: {
      type: Boolean,
      default: true
    },
    observeParents: {
      type: Boolean,
      default: true
    }
  },
  mounted: function () {
    var _this=this;
    new Swiper("."+_this.swiperid, {
      effect: _this.effect,
      loop: _this.loop,
      diraction: _this.diraction,
      autoplay: _this.autoplay,
      paginationType: _this.paginationType,
      pagination: "."+_this.swiperid+" .swiper-pagination",//
      paginationDirection: _this.paginationDirection,
      autoplayDisableOnInteraction: false,
      loopedSlides: _this.loopedSlides,
      slidesPerView: _this.slidesPerView,
      observeParents: _this.observeParents,
      observer: _this.observer
    });
  }
}
</script>

<style scoped>
  @import "../assets/libs/swiper/css/swiper.min.css";
  .swiper-container.swipe01 {padding-top: 1.34rem;}
  .swiper-container.swipe02 .swiper-slide {width: 8.28rem;margin-left: .47rem;}
  .swiper-container.swipe03 .swiper-slide {width: 2.7rem;margin-left: .45rem;}
  .swiper-container.swipe04 .swiper-slide {width: 3rem;margin-left: .32rem;}
  .swiper-container.swipe04 {padding-bottom: .55rem;}
  .swiper-container.swipe05 .swiper-slide {width: 4.8rem;margin-left: .3rem;}
  .swiper-container.swipe06 .swiper-slide {width: 3rem;margin-left: .32rem;}
  .swiper-container.swipe06 {padding-bottom: .55rem;}
  .swiper-container img {width: 100%;border-bottom: 1px solid #DFDFDF;}
  .swiper-slide {font-size: 0;}
</style>



