<template>
  <div class="detail1Wrap">
    <Detaiheader></Detaiheader>

      <div v-for="item in info1" class="wrap clearfix">
        <router-link :to="{name: 'Details', params:{id: item.album_id}}">
        <img :src="item.cover" alt="">
        <h3>{{ item.title }}</h3>
        <p>{{ item.owner.name }}&nbsp;{{ item.begin_time }}</p>
        </router-link>
      </div>
      <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
      <h2 v-show="bol"></h2>

  </div>
</template>

<script>
  import InfiniteLoading from 'vue-infinite-loading'
  import Detaiheader from  '../../components/detaiheader'
  export default {
    components: {
      InfiniteLoading,
      Detaiheader
    },
    data: function () {
      return {
        info: [],
        info1: [],
        j: 0,
        bol: false
      }
    },
    mounted: function () {
      var _this = this;
      this.axios.get("/static/data/onLines.json").then(function (res) {
//        console.log(res.data)
        _this.info = res.data.onlines
      })
    },
    methods: {
      onInfinite() { setTimeout(() => {
        var temp = [];
        this.j++;
        if (this.j*4>=this.info.length){
          this.bol = true;
          this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
          return
        }
        for (var i = 0; i <= this.j*4; i++) {
          temp.push(this.info[i]);
        }
        this.info1 = this.info1.concat(temp);
        this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
      }, 1000);
      },
      showFn: function (){
        alert(1)
      }
    }
  }
</script>

<style scoped>
  h2 {text-align: center;}
  .wrap {padding: 1.34rem 0 .5rem;}
  .detail1Wrap img {float: left;}
  .wrap h3 {font-size: .5rem;}
  .wrap p {font-size: .4rem;}
  a {color: #333;}
</style>



