<template>
  <div class="page5Wrap">
    <Smallnav txt="">
      <span class="left" slot="isSpan"></span>
    </Smallnav>
    <Hot :hotNews="info"></Hot>
  </div>
</template>

<script>
  import Smallnav from "../../components/smallnav"
  import Hot from "../../components/hot"
  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export default {
    components: {
      Smallnav,
      Hot
    },
    data: function (){
      return {
        info: []
      }
    },
    mounted: function (){
      var _this=this;
      this.axios.get("static/data/note.json").then(function(res){
//        console.log(res.data.recommend_feeds)
        _this.info = res.data.slice(6,9)
      });
//
    }
  }
</script>

<style scoped>
  .page5Wrap {background: #fff;margin-bottom: 1.47rem;}
</style>



