<template>
  <div id="app">
    <router-view></router-view>
    <router-view name="Tabbar"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  beforeCreate: function () {
    this.$router.push("/");
  }
}
</script>

<style>
* {margin: 0;padding: 0;}
body {background: #F8F8F8;font-size: 30px;font-size: .3rem;}
.clearfix:after {display: block;content: "200B";font-size: 0;height: 0;clear: both;}
a {text-decoration: none;}
ul,ol {list-style: none;}
i {font-style: normal;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
}
</style>



