<template>
  <div class="wrap">
    <Detaiheader></Detaiheader>

    <div v-for="item in info1" class="wrap clearfix">
      <router-link :to="{name: 'MovieDetai', params:{id: item.id}}">
        <img :src="item.images.medium" alt="">
        <h3>{{ item.title }}</h3>
        <p><a href="javascript:;">{{ item.directors[0].name }}</a></p>
        <p><a href="javascript:;">{{ tostarFn(item.casts) }}</a></p>
        <p><a href="javascript:;">{{ item.genres[0] }}</a></p>
        <p></p>
        <p><a href="javascript:;">{{ item.rating.average }}</a></p>
      </router-link>
    </div>
    <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading"></infinite-loading>
    <h2 v-show="bol"></h2>

  </div>
</template>

<script>
  import InfiniteLoading from 'vue-infinite-loading'
  import Detaiheader from  '../../components/detaiheader'
  export default {
    components: {
      InfiniteLoading,
      Detaiheader
    },
    data: function () {
      return {
        info: [],
        info1: [],
        j: 0,
        bol: false
      }
    },
    mounted: function () {
      var _this = this;
      this.axios.get("/static/data/onShow.json").then(function (res) {
//        console.log(res.data)
        _this.info = res.data.subjects
      })
    },
    methods: {
      onInfinite() { setTimeout(() => {
        var temp = [];
        this.j++;
        if (this.j*4>=this.info.length){
          this.bol = true;
          this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
          return
        }
        for (var i = 0; i <= this.j*5; i++) {
          temp.push(this.info[i]);
        }
        this.info1 = this.info1.concat(temp);
        this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
      }, 1000);
      },
      tostarFn: function (arr){
        var str = "";
        arr.forEach(function (ele){
          str += ele.name+"/"
        })
        return str
      }
    }
  }
</script>

<style scoped>
  .wrap {padding-top: 1.34rem;}
</style>



