<template>
  <div class="page4Wrap">
    <Smallnav txt="">
      <span class="left" slot="isSpan"></span>
    </Smallnav>
    <Hot :hotNews="info"></Hot>
    <Smallnav txt="">
      <span class="left" slot="isSpan"></span>
      <a href="javascript:;" slot="more" @click="changeFn()">></a>
    </Smallnav>
    <Carousel  swiperid="swipe03" :pagination=false :autoplay=0 :loop=false slidesPerView="auto" :loopedSlides="num">
      <div class="swiper-slide" slot="swiper-con" v-for="item in info1">
        <router-link :to="{name: 'Details', params:{id: item.album_id}}">
          <Find :txt="item.shuo_topic">
            <img :src="item.cover" alt="" slot="pic"/>
          </Find>
        </router-link>
      </div>
    </Carousel>
  </div>
</template>

<script>
  import Smallnav from "../../components/smallnav"
  import Hot from "../../components/hot"
  import Carousel from "../../components/carousel"
  import Find from "../../components/find"
  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export default {
    components: {
      Smallnav,
      Hot,
      Carousel,
      Find
    },
    data: function (){
      return {
        info: [],
        info1: [],
        num: 0
      }
    },
    mounted: function (){
      var _this=this;
      this.axios.get("static/data/note.json").then(function(res){
        _this.info = res.data.slice(5,6)
//        console.log(_this.info)
      });
      this.axios.get("static/data/onLines.json").then(function (res){
//        console.log(res.data)
        _this.info1 = res.data.onlines
        _this.num = _this.info1.length
      })
//
    },
    methods: {
      changeFn: function (){
        this.$router.push({path: "detail1"})
      }
    }
  }
</script>

<style scoped>
  .page4Wrap {background: #fff;padding-bottom:  .54rem;margin-bottom: .3rem;}
</style>



