<template>
  <div class="page3Wrap">
    <Smallnav txt="">
      <span class="left" slot="isSpan"></span>
    </Smallnav>
    <Hot :hotNews="info"></Hot>
    <Smallnav txt="tutorabc">
      <img src="../../assets/images/ic_hottest_keyword.png" alt="" class="mid" slot="icon"/>
      <a href="javascript:;" slot="more" class="btn"> ></a>
    </Smallnav>
    <Hot :hotNews="info1"></Hot>
  </div>
</template>

<script>
  import Smallnav from "../../components/smallnav"
  import Hot from "../../components/hot"
  import request from 'superagent'
  import jsonp from 'superagent-jsonp'
  export default {
    components: {
      Smallnav,
      Hot
    },
    data: function (){
      return {
        info: [],
        info1: []
      }
    },
    mounted: function (){
      var _this=this;
      this.axios.get("static/data/note.json").then(function(res){
        _this.info = res.data.slice(2,3)
        _this.info1 = res.data.slice(3,5)
//        console.log(_this.info)
      });
//
    }
  }
</script>

<style scoped>
  .page3Wrap {background: #fff;margin-bottom: .3rem;}
</style>



